const ipPortReg = /http:\/\/(.*?)\//;

export function getRuntimeBaseUrl() {
    const currentScript = window.document.currentScript;
    let baseUrl = "";
    if (currentScript && currentScript.src) {
        const ret = ipPortReg.exec(currentScript.src);
        if (ret) {
            baseUrl = `http://${ret[1]}`
        }
    }
    return baseUrl;
}